@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
.wrapper {
  position: relative;
  height: 140px;
  background: #fff; }

.loader-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  cursor: pointer; }

.loader-circle {
  width: 120px;
  height: 120px;
  display: inline-block;
  z-index: 99;
  position: relative;
  -webkit-transition: .3s ease;
  transition: .3s ease; }

.loader-circle-inner {
  width: 100%;
  height: 100%;
  -webkit-animation: rotate 3.5s linear infinite;
          animation: rotate 3.5s linear infinite;
  -webkit-transform-origin: center center;
          transform-origin: center center;
  margin: auto; }

.path {
  stroke-dasharray: 1, 100;
  stroke-dashoffset: 0px;
  stroke-linecap: square;
  stroke: rgba(85, 89, 144, 0.51);
  -webkit-animation: dash 1.7s linear infinite;
          animation: dash 1.7s linear infinite;
  -webkit-transition: .3s stroke ease;
  transition: .3s stroke ease; }

@-webkit-keyframes rotate {
  to {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg); } }

@keyframes rotate {
  to {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg); } }

@-webkit-keyframes dash {
  0% {
    stroke-dasharray: 5, 100; }
  25% {
    stroke-dasharray: 30, 100;
    stroke-dashoffset: -30px; }
  50% {
    stroke-dasharray: 45, 100;
    stroke-dashoffset: -45px; }
  75% {
    stroke-dasharray: 50, 100;
    stroke-dashoffset: -50px; }
  100% {
    stroke-dasharray: 90, 100;
    stroke-dashoffset: -90px; } }

@keyframes dash {
  0% {
    stroke-dasharray: 5, 100; }
  25% {
    stroke-dasharray: 30, 100;
    stroke-dashoffset: -30px; }
  50% {
    stroke-dasharray: 45, 100;
    stroke-dashoffset: -45px; }
  75% {
    stroke-dasharray: 50, 100;
    stroke-dashoffset: -50px; }
  100% {
    stroke-dasharray: 90, 100;
    stroke-dashoffset: -90px; } }

.list {
  font-family: 'Roboto', sans-serif;
  color: #4c4c4c;
  max-width: 400px;
  margin: 0 auto;
  box-shadow: 0 0 2px #4c4c4c;
  border-radius: 0px; }

.list-item {
  padding: 15px 10px;
  border-bottom: 1px solid #dfdfdf;
  display: flex;
  justify-content: space-between;
  -webkit-transition: .08s linear;
  transition: .08s linear;
  position: relative;
  align-items: center; }
  .list-item .text {
    font-weight: 300;
    flex: 1 1; }
  .list-item .time-to-end {
    margin-right: 10px; }
  .list-item .progress-line {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 100%;
    width: 0%;
    background: #2ea02410; }
  .list-item:hover {
    background: #f7f7f7; }

@-webkit-keyframes fade {
  0% {
    background: #72cffa; } }

@keyframes fade {
  0% {
    background: #72cffa; } }

@-webkit-keyframes timeLine {
  0% {
    width: 100%; } }

@keyframes timeLine {
  0% {
    width: 100%; } }

.button-close {
  width: 16px;
  height: 16px;
  cursor: pointer;
  -webkit-transition: .1s linear;
  transition: .1s linear;
  color: #adadad;
  opacity: .5; }
  .button-close:hover {
    color: #000000;
    opacity: 1; }
  .button-close svg {
    fill: currentColor; }

.input-container .add-item-form {
  display: flex;
  flex-wrap: wrap; }

.input-container input {
  padding: 15px 10px;
  margin: 0;
  width: 100%;
  font-size: 14px;
  box-sizing: border-box;
  color: currentColor;
  outline: none;
  border-radius: 0;
  border: 1px solid #dfdfdf;
  -webkit-transition: .15s;
  transition: .15s; }
  .input-container input.type-text {
    flex: 2 1; }
  .input-container input.type-number {
    flex: 1 1; }
  .input-container input:active, .input-container input:focus {
    border: 1px solid #a7a7a7; }

